.container {
    text-align: center;
}

.title {
    margin-top: 50px;
    color: white;
    font-size: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.title span {
    color: #26ffcb;
    padding-left: 15px;
}

.reset {
    width: 250px;
    height: 100px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 50px;
    background: #1f3540;
    font-size: 26px;
    color: #26ffcb;
    margin-top: 25px;
    margin-bottom: 50px;
}

@media(min-width: 416px) {

    .title {
        font-size: 40px;
        display: flex;
        text-align: center;
        margin-top: 50px;
        margin-bottom: 50px;
        margin-left: 150px;
        margin-rigth: 150px;
    }

    .reset {
        margin-left: 150px;
    }
}

@media(min-width: 1920px) {

    .title {
        margin-top: 50px;
        color: white;
        font-size: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .reset {
        width: 250px;
        height: 100px;
        border: none;
        outline: none;
        cursor: pointer;
        border-radius: 50px;
        background: #1f3540;
        font-size: 26px;
        color: #26ffcb;
        margin-top: 25px;
        margin-bottom: 50px;
    }
}

.board {
    height: 600px;
    width: 564px;
    display: flex;
    margin: auto;
}

.boxes {
    display: flex;
    height: 180px;
    width: 180px;
    background: #1f3540;
    border: 4px solid #0f1b21;
    border-radius: 12px;
    cursor: pointer;
}

.boxes img {
    margin: 50px 50px;
}

.title img {
    padding: 20px 20px;
    height: 50px;
}